import React, {  useContext, useState } from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
// import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
// import PropTypes from "prop-types";
// import AppBar from '@mui/material/AppBar';
import AccountCircle from "@mui/icons-material/AccountCircle";
import Divider from "@mui/material/Divider";
import Drawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemText from "@mui/material/ListItemText";
import MenuIcon from "@mui/icons-material/Menu";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";

import Button from "@mui/material/Button";
// import { Stack } from "@mui/system";
import { Link } from "react-router-dom";
// import LoginSharpIcon from "@mui/icons-material/LoginSharp";
// import HowToRegSharpIcon from "@mui/icons-material/HowToRegSharp";
// import ShoppingCartSharpIcon from "@mui/icons-material/ShoppingCartSharp";
import { UserContext } from "../../App";

const Navbar = (props) => {
  const [logOutSt, setLogOutSt] = useState(false);
  const userLoginSt = useContext(UserContext);
  console.log(userLoginSt);

  const logOut = (e) => {
    console.log("Hello");
    e.preventDefault();
    if (window.confirm("Are You Sure Want to Logout?") === true) {
      // localStorage.clear();
      localStorage.removeItem("username");
      props.onLogOutClick(false);
      setLogOutSt(!logOutSt);
    }
  };

  const drawerWidth = 240;
  const navItems = ["Home", "About", "Products", "Contact"];
  // const navItemsOther = ['Cart', 'About','Products', 'Contact' ];
  const [auth, setAuth] = React.useState(true);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const { windo } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ textAlign: "center" }}>
      <Typography variant="h6" sx={{ my: 2 }}>
        ONLINE MALL
      </Typography>

      <Divider />
      <List>
        {navItems.map((item) => (
          <ListItem
            key={item}
            component={Link}
            to={item === "Home" ? "/" : `/${item}`}
            disablePadding
          >
            <ListItemButton sx={{ textAlign: "center" }}>
              <ListItemText primary={item} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );

  const container =
    windo !== undefined ? () => window().document.body : undefined;

  // const handleMenu = (event) => {
  //   setAnchorEl(event.currentTarget);
  // };
  return (
    <>
      <Box
        sx={{
          display: "flex",
          justifyContent: "flex-start",
          height: { sm: "63px", xs: "47px" },
        }}
      >
        <AppBar component="nav">
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              sx={{ mr: 2, display: { md: "none" } }}
            >
              <MenuIcon />
            </IconButton>
            <Typography
              variant="h6"
              component="div"
              sx={{
                flexGrow: 1,
                display: { xs: "none", sm: "none", md: "block" },
              }}
            >
              ONLINE MALL
            </Typography>

            <Box sx={{ display: { xs: "none", sm: "none", md: "block" } }}>
              {navItems.map((item) => (
                <Button
                  component={Link}
                  to={item === "Home" ? "/" : `/${item}`}
                  key={item}
                  sx={{ color: "#fff" }}
                >
                  {item}
                </Button>
              ))}
            </Box>

            <Box sx={{ display: { xs: "block", sm: "block", md: "block" } }}>
              <Button component={Link} to={"/cart"} sx={{ color: "#fff" }}>
                <IconButton
                  size="large"
                  aria-label="account of current user"
                  aria-controls="menu-appbar"
                  aria-haspopup="true"
                  // onClick={handleMenu}
                  color="inherit"
                >
                  <ShoppingCartIcon />
                </IconButton>
              </Button>

              {userLoginSt ? (
                <Button
                  onClick={logOut}
                  component={Link}
                  to="/Login"
                  sx={{ color: "#fff" }}
                >
                  <IconButton
                    size="large"
                    aria-label="account of current user"
                    aria-controls="menu-appbar"
                    aria-haspopup="true"
                    // onClick={handleMenu}
                    color="inherit"
                  >
                    <AccountCircle />
                  </IconButton>
                </Button>
              ) : (
                <Button component={Link} to="/Login" sx={{ color: "#fff" }}>
                  <IconButton
                    size="large"
                    aria-label="account of current user"
                    aria-controls="menu-appbar"
                    aria-haspopup="true"
                    // onClick={handleMenu}
                    color="inherit"
                  >
                    <AccountCircle />
                  </IconButton>
                </Button>
              )}
            </Box>
          </Toolbar>
        </AppBar>
        <Box component="nav">
          <Drawer
            container={container}
            variant="temporary"
            open={mobileOpen}
            onClose={handleDrawerToggle}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
            sx={{
              display: { xs: "block", sm: "block", md: "none" },
              "& .MuiDrawer-paper": {
                boxSizing: "border-box",
                width: drawerWidth,
              },
            }}
          >
            {drawer}
          </Drawer>
        </Box>
      </Box>
    </>
  );
};

export default Navbar;
